import React, { useState, useEffect, useContext, useRef } from 'react';
import { Values, Colors } from './game-engine';
import { SocketContext, GameObject, Player, Card, DiscardedCard, ResultObject, OwnedCard } from './App';
import svg from './svg-cards.svg';
import './Cards.css';
import Cookies from 'universal-cookie';
import Result from './Result';

const cookies = new Cookies();

export default function Game({game: gameInstance, name}: {game: GameObject, name: string}) {

    const socket = useContext(SocketContext);

    const [game, setGame] = useState<GameObject>();
    const [cardsToPutQueue, setCardsToPutQueue] = useState<Card[]>([]);
    const [currentRoundState, setCurrentRoundState] = useState<'NORMAL' | 'BOMB' | 'MULTIPLE' | 'CHALLENGE' | 'NONE'>('NORMAL');
    const [cardsToPlay, setCardsToPlay] = useState<Card[]>([]);
    const [topCards, setTopCards] = useState<Card[]>([]);
    const [currentRoundCards, setCurrentRoundCards] = useState<DiscardedCard[][]>([]);
    const [challengeCards, setChallengeCards] = useState<Card[]>();

    const [player, setPlayer] = useState<Player>();
    const [teammate, setTeammate] = useState<Player>();
    const [opponents, setOpponents] = useState<[Player, Player]>();

    const [res, setRes] = useState<ResultObject>();

    const [consoleMessages, setConsoleMessages] = useState<string[]>([]);

    const _console = useRef<HTMLDivElement>(null);

    const addMessage = (message: string) => {
        setConsoleMessages(prevMessages => [...prevMessages, message]);
        setTimeout(() => {
            _console.current!.scrollTop = _console.current!.scrollHeight + 10
        }, 0);
    }
    
    useEffect(() => {
        setGame(gameInstance);
        socket.on('dispatch', ({ game: gameInstance, type, payload }: { game: GameObject, type: string, payload: {[keys: string]: any} }) => {
            setGame(gameInstance);
            switch(type){
                case 'cardDraw':
                    addMessage(`${payload.player.name} has drawn a card!`);
                    break;
                case 'cardTake':
                    addMessage(`${payload.player.name} has taken remaining cards!`);
                    break;
                case 'cardPut':
                    addMessage(`${payload.player.name} has put down six cards for the defending team!`);
                    break;
                case 'setMasterColor':
                    addMessage(`${payload.player.name} used their ${Values[payload.card._value]} ${Colors[payload.card._color]} card to set the master color!`);
                    break;
                case 'cardPlay':
                    setCurrentRoundState('NORMAL');
                    setCurrentRoundCards(prevCards => [...prevCards, [payload.card]]);
                    addMessage(`${payload.player.name} has played card ${Values[payload.card.card._value]} ${Colors[payload.card.card._color]}!`);
                    break;
                case 'roundEnd':
                    setCurrentRoundState('NORMAL');
                    setCurrentRoundCards([]);
                    setCardsToPlay([]);
                    addMessage(`A round has ended with the ${payload.winner.role} team winning. ${payload.discardedCards.length} cards discarded and ${payload.takenCards.length} cards taken.`);
                    break;
                case 'strongBomb':
                    setCurrentRoundState('BOMB');
                    setCurrentRoundCards(prevCards => [...prevCards, payload.cards]);
                    addMessage(`${payload.player.name} has played a strong bomb!`);
                    break;
                case 'weakBomb':
                    setCurrentRoundState('BOMB');
                    setCurrentRoundCards(prevCards => [...prevCards, payload.cards]);
                    addMessage(`${payload.player.name} has played a weak bomb!`);
                    break;
                case 'multipleCardPlay':
                    setCurrentRoundState('MULTIPLE');
                    setCurrentRoundCards(prevCards => [...prevCards, payload.cards]);
                    addMessage(`${payload.player.name} has played multiple cards!`);
                    break;
                case 'forMultipleCardPlay':
                    setCurrentRoundCards(prevCards => [...prevCards, payload.cards]);
                    addMessage(`${payload.player.name} has played multiple cards!`);
                    break;
                case 'forBombCardPlay':
                    setCurrentRoundCards(prevCards => [...prevCards, payload.cards]);
                    addMessage(`${payload.player.name} has played in response to a bomb!`);
                    break;
                case 'requestChallenge':
                    setChallengeCards(payload.cards);
                    setCurrentRoundState('CHALLENGE');
                    addMessage(`${payload.player.name} is trying to play multiple cards!`);
                    break;
                case 'challengeFailed':
                    addMessage(`${payload.playerName} is not challenging!`);
                    break;
                case 'challengeSuccess':
                    addMessage(`${payload.playerName} has successfully challenged!`);
                    break;
                case 'gameEnd':
                    cookies.remove('name');
                    cookies.remove('room');
                    addMessage(`The game has ended with the attacking team holding ${payload.score} score and ${payload.flipCards ? 'flipping the hidden cards' : 'failing to flip the final cards'}!`);
                    setRes({
                        flip: payload.flipCards,
                        isOpen: true,
                        score: payload.score,
                        discardedCards: gameInstance._discardedPile,
                        takenCards: gameInstance._teams.find(team => team.role === 'ATTACK')!._ownedCards,
                        hiddenCards: gameInstance._hiddenCards
                    });
                    break;
                case 'drawPhase':
                    addMessage('Draw phase now');
                    break;
                case 'takePhase':
                    addMessage('Take phase now');
                    break;
                case 'putPhase':
                    addMessage('Put phase now');
                    break;
                case 'playPhase':
                    addMessage('Play phase now');
                    break;
                case 'playerLeave':
                    addMessage(`${payload.name} has left the game!`);
                    break;
                case 'playerJoin':
                    addMessage(`${payload.name} has joined back!`);
                    break;
                case 'error':
                    addMessage(`Error! ${payload.message}`);
                    break;
            }
            console.log({ gameInstance, type, payload });
        });
    }, []);

    useEffect(() => {
        console.log({game, player, teammate, opponents});
        if(game){
            let player = game._players.filter(_player => {
                return _player.name === name;
            })[0];

            let teammate = game._players.filter(_player => {
                return (!(_player.name === name) && _player.role === player.role);
            })[0];

            let opponents = game._players.filter(_player => {
                return !(_player.role === player.role);
            });

            if(game._players.findIndex(_player => _player.name === name) < 2){
                opponents.reverse();
            }

            let cardsPerPlayer = game._currentRoundCards.length / game.currentRoundCount;

            for(let i=0;i<game._currentRoundCards.length;i+=cardsPerPlayer){
                setCurrentRoundCards(prevCards => [...prevCards, game._currentRoundCards.slice(i, i+cardsPerPlayer)]);
            }

            setTopCards(game._currentRoundTopCards);
            if(!currentRoundState) setCurrentRoundState(cardsPerPlayer > 1 ? 'MULTIPLE' : 'NORMAL');
            setPlayer(player);
            setTeammate(teammate);
            setOpponents(opponents as [Player, Player]);
        }
    }, [game])

    const handleDrawCard = () => {
        socket.emit('requestDraw');
    }

    const handleTakeCards = () => {
        socket.emit('requestTake', {player});
    }

    const handleSetMasterColor = (card: Card) => {
        if(!(card._value === game!.masterNumber) || card._color === Colors.JOKER){
            addMessage(`Your card ${card._color} ${card._value} cannot be used to set the master color!`);
        }else{
            socket.emit('requestSetMasterColor', {card, player});
        }
    } 

    const handleAddCardToPutQueue = (card: Card) => {
        if(!!(cardsToPutQueue.find(_card => _card._color === card._color && _card._value === card._value))){
            let newQueue = [...cardsToPutQueue];
            let cardIndex = newQueue.findIndex(_card => _card._color === card._color && _card._value === card._value);
            newQueue.splice(cardIndex, 1);
            setCardsToPutQueue(newQueue);
        }else{
            setCardsToPutQueue(prevCards => [...prevCards, card]);
        }
    }

    const handlePutCards = () => {
        if(!(cardsToPutQueue.length === 6)){
            addMessage(`You have ${cardsToPutQueue.length}/6 cards in your queue!`);
        }else{
            socket.emit('requestPut', {player, cards: cardsToPutQueue});
        }
    }

    const handlePlaceDownCard = (card: Card) => {
        if(!!(cardsToPlay.find(_card => _card._color === card._color && _card._value === card._value))){
            let newCards = [...cardsToPlay];
            let cardIndex = newCards.findIndex(_card => _card._color === card._color && _card._value === card._value);
            newCards.splice(cardIndex, 1);
            setCardsToPlay(newCards);
        }else{
            setCardsToPlay(prevCards => [...prevCards, card]);
        }
    }

    const handlePlayCard = () => {
        if(cardsToPlay.length > 1){
            addMessage('can only play 1 card');
        }else if(cardsToPlay.length === 0){
            addMessage('pick a card');
        }else{
            socket.emit('requestPlay', {card: cardsToPlay[0]});
        }
    }

    const handlePlayMultipleCards = () => {
        if(cardsToPlay.length < 2){
            addMessage('play 1 card plz');
        }else{
            socket.emit('requestPlayMultiple', {cards: cardsToPlay});
        }
    }

    const handlePlayBomb = () => {
        if(!(cardsToPlay.length === 4)){
            addMessage('you can only throw bombs with 4 cards!');
        }else{
            let allSameValue = !cardsToPlay.find(card => !(card._value === cardsToPlay[0]._value));
            if(!allSameValue){
                addMessage('you can only throw bombs with cards of same value');
            }else{
                socket.emit('requestPlayBomb', {cards: cardsToPlay});
            }
        }
    }

    const handlePlayForMultiple = () => {
        if(!(cardsToPlay.length === topCards.length)){
            addMessage(`expected ${topCards.length} cards, got ${cardsToPlay.length} instead.`);
        }else{
            socket.emit('requestPlayForMultiple', {cards: cardsToPlay});
        }
    }

    const handlePlayForBomb = () => {
        if(!(cardsToPlay.length === 4)){
            addMessage('you must play 4 cards!');
        }else{
            socket.emit('requestPlayForBomb', {cards: cardsToPlay});
        }
    }

    const handleChallenge = () => {
        if(!(cardsToPlay.length === 1)){
            addMessage('can only challenge with 1 card');
        }else{
            socket.emit('challenge', {card: cardsToPlay[0], player});
            setCurrentRoundState('NORMAL');
            setCurrentRoundState('NONE');
        }
    }

    const handleNoChallenge = () => {
        socket.emit('noChallenge', {playerName: player!.name});
        setCurrentRoundState('NORMAL');
    }

    const handleLeave = () => {
        if(window.confirm('Are you sure you want to leave this game?')){
            cookies.remove('room');
            cookies.remove('game');
            window.location.reload();
        }
    }

    if(!game || !player || !teammate || !opponents){
        return <div>Loading...</div>
    }

    return (
        <>
            <div className="side-bar">
                <div>
                    Playing as: {player.name}
                </div>
                <div>
                    Master number: {Values[game.masterNumber] || 'JOKER'}
                </div>
                <div>
                    Master color: {Colors[game.masterColor] || 'Not yet selected'}
                </div>
                <div>
                    Current phase: {game.phase}
                </div>
                <div>
                    You are on the {player.role}ING team!
                </div>
                <div>
                        {game.phase === 'PUTTING' && player.hand.length === 18 && 'Cards you are putting down:'}
                        <div className="selected-cards">
                            {game.phase === 'PUTTING' && player.hand.length === 18 && cardsToPutQueue.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper-small">
                                        <div className="card-wrapper-active-small" onClick={() => handleAddCardToPutQueue(card)}>
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                            {game.phase === 'PLAYING' && (game._currentPlayer.name === player.name || currentRoundState === 'CHALLENGE') && (
                                <>
                                    <div>Currently selected cards:</div>
                                    {cardsToPlay.map((card, idx) => {
                                        return (
                                            <div key={idx} className="outer-wrapper-small">
                                                <div className="card-wrapper-active-small" onClick={() => handlePlaceDownCard(card)}>
                                                    <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                                </div>
                                            </div>
                                        )
                                    })}
                                </>
                            )}
                        </div>
                    </div>
                    <div className="leave-game">
                        <button onClick={() => handleLeave()}>Leave this game</button>
                    </div>
            </div>
            <div className="game-container">
                <div className="teammate">
                    <div className="other-player-name">{teammate.name}</div>
                    <div className="other-hand-container">
                        {teammate.hand.map((_, idx) => {
                            return (
                                <div key={idx} className="outer-wrapper-small">
                                    <div key={idx} className="card-wrapper-small">
                                        <img className="BACK" src={svg}></img>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                    <div className="other-role">
                        {teammate.role}ING
                    </div>
                </div>
                <>
                {opponents.map((opponent, idx) => {
                    return (<div key={idx} className={`opponent-${idx}`}>
                        <div className="other-player-name">{opponent.name}</div>
                        <div className="other-hand-container">
                            {opponent.hand.map((_, idx) => {
                                return (
                                    <div key={idx} className="outer-wrapper-small">
                                        <div key={idx} className="card-wrapper-small">
                                            <img className="BACK" src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                        <div className="other-role">
                            {opponent.role}ING
                        </div>
                    </div>)
                })}
                </>
                <div className="self">
                    {/* Space for drawing/taking buttons */}
                    <div className="button-2">
                        <div>
                            {game.phase === 'DRAWING' && game._currentPlayer.name === player.name && <button className="button-2" onClick={() => handleDrawCard()}>Draw</button>}
                        </div>
                        <div>
                            {game.phase === 'TAKING' && player.role === 'DEFEND' && <button className="button-2" onClick={() => handleTakeCards()}>Take remaining</button>}
                        </div>
                        <div>
                            {game.phase === 'PUTTING' && player.hand.length === 18 && <button className="button-2" onClick={() => handlePutCards()}>Put down selected</button>}
                        </div>
                    </div>
                    {/* Space for list of cards (to declare master, to put and to play) */}
                    <div className="hand-container">
                        <>
                            {game.phase === 'DRAWING' && player.hand.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper">
                                        <div key={idx} className="card-wrapper-active" onClick={() => handleSetMasterColor(card)}>
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </>
                        <>
                            {game.phase === 'TAKING' && player.hand.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper">
                                        <div key={idx} className="card-wrapper">
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </>
                        <>
                            {game.phase === 'PUTTING' && player.hand.length === 18 && player.hand.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper">
                                        <div key={idx} className="card-wrapper-active" onClick={() => handleAddCardToPutQueue(card)}>
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </>
                        <>
                            {game.phase === 'PUTTING' && !(player.hand.length === 18) && player.hand.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper">
                                        <div className="card-wrapper">
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </>
                        <>
                            {game.phase === 'PLAYING' && (game._currentPlayer.name === player.name || currentRoundState === 'CHALLENGE') && player.hand.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper">
                                        <div className="card-wrapper-active" onClick={() => handlePlaceDownCard(card)}>
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </>
                        <>
                            {game.phase === 'PLAYING' && !(game._currentPlayer.name === player.name) && !(currentRoundState === 'CHALLENGE') && player.hand.map((card: Card, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper">
                                        <div className="card-wrapper">
                                            <img className={`${Colors[card._color]}-${Values[card._value]}`} src={svg}></img>
                                        </div>
                                    </div>
                                )
                            })}
                        </>
                    </div>
                    <div>
                        {currentRoundState === 'CHALLENGE' && !(game._currentPlayer.name === player.name) && (
                            <>
                                <div className="challenge-desc">{game._currentPlayer.name} is playing multiple cards. Challenge?</div>
                                <button className="button-2" onClick={() => handleChallenge()}>Challenge</button>
                                <button className="button-3" onClick={() => handleNoChallenge()}>Pass</button>
                            </>
                        )}
                        {game.phase === 'PLAYING' && currentRoundState !== 'NONE' && game._currentPlayer.name === player.name && currentRoundState === 'NORMAL' && game.currentRoundCount === 0 && (
                            <>
                                <button className="button-1" onClick={() => handlePlayCard()}>Play</button>
                                <button className="button-2" onClick={() => handlePlayMultipleCards()}>Play many</button>
                                <button className="button-3" onClick={() => handlePlayBomb()}>Play bomb</button>
                            </>
                        )}
                        {game.phase === 'PLAYING' && game._currentPlayer.name === player.name && currentRoundState === 'NORMAL' && game.currentRoundCount > 0 && (
                            <>
                                <button className="button-1" onClick={() => handlePlayCard()}>Play</button>
                            </>
                            )}
                        {game.phase === 'PLAYING' && game._currentPlayer.name === player.name && currentRoundState === 'BOMB' && (
                            <>
                                <button className="button-3" onClick={() => handlePlayForBomb()}>Play</button>
                            </>
                        )}
                        {game.phase === 'PLAYING' && game._currentPlayer.name === player.name && currentRoundState === 'MULTIPLE' && (                                    <>
                                <button className="button-2" onClick={() => handlePlayForMultiple()}>Play</button>
                            </>
                        )}
                    </div>
                </div>
                <div className="game-info-container">
                    <div className="hidden-cards-reveal">
                        {game.phase === 'PUTTING' && game._hiddenCards.map((card: Card, idx: number) => {
                            return (
                                <div key={idx} className="outer-wrapper">
                                    <div className="card-wrapper">
                                        <img src={svg} className={`${Colors[card._color]}-${Values[card._value]}`}></img>
                                    </div>
                                </div>
                            )})}
                    </div>
                    <div className="hidden-cards-reveal">
                        {currentRoundState === 'CHALLENGE' && <div className="title"></div>}
                        {currentRoundState === 'CHALLENGE' && challengeCards && challengeCards.map((card: Card, idx: number) => {
                            return (
                                <div key={idx} className="outer-wrapper">
                                    <div className="card-wrapper">
                                        <img src={svg} className={`${Colors[card._color]}-${Values[card._value]}`}></img>
                                    </div>
                                </div>
                            )})}
                    </div>
                    <div className="draw-pile">
                        {(game.phase === 'DRAWING' || game.phase === 'TAKING') && game.drawPile.shuffle.map((_: any, idx: number) => {
                            return (
                                <div key={idx} className="outer-wrapper-medium" style={{
                                    'position': 'absolute',
                                    'zIndex': -1*idx,
                                    'top': '0',
                                    'left': '0',
                                    'transform': `translateY(${idx * 0.4}px) rotateX(80deg)`
                                }}>
                                    <div className="card-wrapper-medium">
                                        <img src={svg} className="BACK"></img>
                                    </div>
                                </div>
                            )})}
                    </div>
                    <div className="current-cards">
                        <div className="teammate-card">
                            {currentRoundCards.find(cards => cards[0].playedBy.name === teammate.name) && (
                                currentRoundCards.find(cards => cards[0].playedBy.name === teammate.name)!.map((card, idx) => {
                                    return (
                                        <div key={idx} className="outer-wrapper-medium">
                                            <div className="card-wrapper-medium">
                                                <img src={svg} className={`${Colors[card.card._color]}-${Values[card.card._value]}`}></img>
                                            </div>
                                        </div>
                                    )
                                })
                            )}
                        </div>
                        <div className="op-1-card">
                            {currentRoundCards.find(cards => cards[0].playedBy.name === opponents[0].name) && (
                                currentRoundCards.find(cards => cards[0].playedBy.name === opponents[0].name)!.map((card, idx) => {
                                    return (
                                        <div key={idx} className="outer-wrapper-medium">
                                            <div className="card-wrapper-medium">
                                                <img src={svg} className={`${Colors[card.card._color]}-${Values[card.card._value]}`}></img>
                                            </div>
                                        </div>
                                    )
                                })
                            )}
                        </div>
                        <div className="op-2-card">
                            {currentRoundCards.find(cards => cards[0].playedBy.name === opponents[1].name) && (
                                currentRoundCards.find(cards => cards[0].playedBy.name === opponents[1].name)!.map((card, idx) => {
                                    return (
                                        <div key={idx} className="outer-wrapper-medium">
                                            <div className="card-wrapper-medium">
                                                <img src={svg} className={`${Colors[card.card._color]}-${Values[card.card._value]}`}></img>
                                            </div>
                                        </div>
                                    )
                                })
                            )}
                        </div>
                        <div className="self-card">
                            {currentRoundCards.find(cards => cards[0].playedBy.name === player.name) && (
                                currentRoundCards.find(cards => cards[0].playedBy.name === player.name)!.map((card, idx) => {
                                    return (
                                        <div key={idx} className="outer-wrapper-medium">
                                            <div className="card-wrapper-medium">
                                                <img src={svg} className={`${Colors[card.card._color]}-${Values[card.card._value]}`}></img>
                                            </div>
                                        </div>
                                    )
                                })
                            )}
                        </div>
                    </div>
                    {game.phase === 'PLAYING' && (
                        <div className="taken-cards">
                            {game._teams.find(team => team.role === 'ATTACK')!._ownedCards.length !== 0 && <div className="description">Cards taken</div>}
                            {game._teams.find(team => team.role === 'ATTACK')!._ownedCards.map((card: OwnedCard, idx: number) => {
                            return (
                                <div key={idx} className="outer-wrapper-small">
                                    <div className="card-wrapper-small">
                                        <img src={svg} className={`${Colors[card.card._color]}-${Values[card.card._value]}`}></img>
                                    </div>
                                </div>
                            )})}
                        </div>
                    )}
                    {game.phase === 'PLAYING' && (
                        <div className="discarded-cards">
                            {game._discardedPile.length !== 0 && <div className="description">Cards discarded</div>}
                            {game._discardedPile.map((_: any, idx: number) => {
                                return (
                                    <div key={idx} className="outer-wrapper-medium" style={{
                                        'position': 'absolute',
                                        'zIndex': -1*idx,
                                        'top': '0',
                                        'left': '0',
                                        'transform': `translateY(${idx * 0.4}px) rotateX(80deg)`
                                    }}>
                                        <div className="card-wrapper-medium">
                                            <img src={svg} className="BACK"></img>
                                        </div>
                                    </div>
                            )})}
                        </div>
                    )}
                </div>
            </div>
            <div ref={_console} className="console">
                {consoleMessages.map((message: string, idx: number) => {
                    return (
                        <div key={idx} className="console-message">
                            {message}
                        </div>
                    )
                })}
            </div>
            {res && <Result isOpen={res.isOpen} discardedCards={res.discardedCards} takenCards={res.takenCards} score={res.score} flip={res.flip} hiddenCards={res.hiddenCards}/>}
        </>
    )
}
