import React, { useEffect, useState } from 'react';
import './App.css';
import socketIOClient from 'socket.io-client';
import Home from './Home';
import { Values, Colors } from './game-engine';
import Cookies from 'universal-cookie';
import Game from './Game';
import GameChinese from './GameChinese';

const HOST = window.location.origin.replace(/^http/, 'ws');

const socket = socketIOClient(HOST);

export const SocketContext = React.createContext<SocketIOClient.Socket>(socket);

export interface ResultObject {
  isOpen: boolean,
  discardedCards: DiscardedCard[],
  takenCards: OwnedCard[],
  flip: boolean,
  score: number,
  hiddenCards: Card[]
}

export interface OwnedCard {
  card: Card,
  points: number
}

export interface Card {
  _value: Values,
  _color: Colors,
  wasHidden: boolean;
}

export interface Player {
  hand: Card[],
  name: string,
  role: 'DEFEND' | 'ATTACK',
  isReady: boolean
}

export interface OwnedCard {
  card: Card,
  points: number
}

export interface Team {
  _players: Player[],
  _ownedCards: OwnedCard[],
  role: 'ATTACK' | 'DEFEND'
}

export interface DiscardedCard {
  card: Card,
  playedBy: Player
}

export interface GameObject {
  _players: Player[],
  _currentRoundCards: DiscardedCard[],
  _currentRoundTopCards: Card[],
  _currentPlayer: Player,
  _teams: [Team, Team],
  _discardedPile: DiscardedCard[],
  _hiddenCards: Card[],
  currentRoundCount: number,
  roundCount: number,
  masterNumber: number,
  phase: 'DRAWING' | 'TAKING' | 'PUTTING' | 'PLAYING',
  [x: string]: any
}

export interface Room {
  name: string | undefined,
  game: GameObject | undefined,
  players: Player[],
  currentPlayers: Player[]
}

const cookies = new Cookies();

function App() {

  const [game, setGame] = useState<GameObject>();

  useEffect(() => {
    let name = cookies.get('name');
    let room = cookies.get('room');
    let lang = cookies.get('lang');

    console.log({name, room, lang});

    if(name && room && lang){
      socket.emit('recoverRoom', {name, room});
    }

    socket.on('roomRecover', ({game}: {game: GameObject}) => {
      setGame(game);
    });
  }, []);

  if(game){
    return (
      <SocketContext.Provider value={socket}>
        <div className="App">
          <div className="warning">
              <p>Your screen size is too small for the game. Please switch to Landscape view or change to a larger device!</p>
          </div>
          {cookies.get('lang') === 'ENG' ? <Game game={game} name={cookies.get('name')} /> : <GameChinese game={game} name={cookies.get('name')} />}
      </div>
      </SocketContext.Provider>
    )}

  return (
    <SocketContext.Provider value={socket}>
      <div className="App">
          <div className="warning">
              <p>Your screen size is too small for the game. Please switch to Landscape view or change to a larger device!</p>
          </div>
          <Home />
      </div>
    </SocketContext.Provider>
  );
}

export default App;
