import React from 'react';
import {Values, Colors} from './game-engine';
import { DiscardedCard, OwnedCard, Card } from './App';
import svg from './svg-cards.svg';

export default function Result({isOpen, discardedCards, takenCards, flip, score, hiddenCards}: {isOpen: boolean, discardedCards: DiscardedCard[], takenCards: OwnedCard[], flip: boolean, score: number, hiddenCards: Card[]}) {    
    return (
        <div className="result" style={{visibility: isOpen? 'visible' : 'hidden'}}>
            <div className="title">
                This game has ended!
            </div>
            <div className="summary">
                <p>Total score of attacking team: {score}</p>
                <p>Hidden card flip: {flip ? 'true': 'false'}</p>
                <p>Number of discarded cards: {discardedCards.length}</p>
                <p>Number of taken cards: {takenCards.length}</p>
            </div>
            <div className="cards-summary">
                <p>Cards hidden:</p>
                <div className="taken-cards-container">
                    {hiddenCards.map(card => {
                        return (
                        <div className="card">
                            <div className="outer-wrapper-small">
                                <div className="card-wrapper-small">
                                    <img src={svg} className={`${Colors[card._color]}-${Values[card._value]}`}></img>
                                </div>
                            </div>
                        </div>
                        )})}
                </div>
            </div>
            <div className="cards-summary">
                <p>Cards successfully taken:</p>
                <div className="taken-cards-container">
                    {takenCards.map(card => {
                        return (
                        <div className="card">
                            <div className="outer-wrapper-small">
                                <div className="card-wrapper-small">
                                    <img src={svg} className={`${Colors[card.card._color]}-${Values[card.card._value]}`}></img>
                                </div>
                            </div>
                            <div className="card-played-by">
                                <div>{card.points}</div>
                            </div>
                        </div>
                        )})}
                </div>
            </div>
            <div className="exit">
                <button onClick={() => window.location.reload()}>Restart game</button>
            </div>
        </div>
    )
}
