import React, { useContext, useEffect, useState, useRef } from 'react';
import { SocketContext, GameObject, Room } from './App';
import { Values } from './game-engine';
import Game from './Game';
import GameChinese from './GameChinese';
import Cookies from 'universal-cookie';

const cookies = new Cookies();

export default function Home() {

    const socket = useContext(SocketContext);

    const [name, setName] = useState<string>();
    const [role, setRole] = useState<'ATTACK' | 'DEFEND'>('ATTACK');
    const [room, setRoom] = useState<string>();
    const [lang, setLang] = useState<'ENG' | 'CHN'>(cookies.get('lang') || 'ENG');

    const [masterNumber, setMasterNumber] = useState<string>("2");

    const [game, setGame] = useState<boolean | object>(false);

    const [hasRoom, setHasRoom] = useState<boolean | Room>(false);
    const [isReady, setIsReady] = useState<boolean>(false);

    const alert = useRef<HTMLDivElement>(null);
    const langRef = useRef<'CHN' | 'ENG'>('ENG');
    const nameRef = useRef<string>();
    const roomRef = useRef<string>();

    const setCookies = () => {
        cookies.set('name', nameRef.current);
        cookies.set('room', roomRef.current);
        cookies.set('lang', langRef.current);
    }

    useEffect(() => {
        socket.on('gameStart', ({game}: {game: object}) => {
            setCookies();
            setGame(game);
        });

        socket.on('roomJoin', (room: Room) => {
            setHasRoom(room);
        });

        socket.on('roomUpdate', (room: Room) => {
            setHasRoom(room);
        });

        socket.on('oops', (e: string) => {
            if(!alert.current) return;
            switch(e){
                case 'DUPE_NAME':
                    alert.current!.innerHTML = (langRef.current === 'ENG' ? 'Someone in this room already has your name!' : '');
                    break;
                case 'FULL':
                    alert.current!.innerHTML = (langRef.current === 'ENG' ? 'This room is full!' : '');
                    break;
                case 'IN_GAME':
                    alert.current!.innerHTML = (langRef.current === 'ENG' ? 'This room is already in-game!' : '');
                    break;
                case 'DUPE_ROLE':
                    alert.current!.innerHTML = (langRef.current === 'ENG' ? 'There are already two players taking your role!' : '');
                    break;
            }
        });
    }, []);

    const handleSubmit = (e: React.FormEvent<HTMLButtonElement>) => {
        e.preventDefault();

        if(!name || !role || !room){
            alert.current!.innerHTML = lang === 'ENG' ? 'Please fill in all required fields!' : '';
            return;
        }

        if(name.length > 14){
            alert.current!.innerHTML = lang === 'ENG' ? 'Your name cannot be longer than 14 characters!' : '14';
            return;
        }

        socket.emit('setName', {name, role, room});
    }

    const toggleReady = () => {
        if(hasRoom){
            socket.emit('toggleReady', masterNumber);
            setIsReady(!isReady);
        }
    }

    if(game){
        if(lang === 'ENG'){
            return <Game game={game as GameObject} name={name!}/>;
        }else{
            return <GameChinese game={game as GameObject} name={name!}/>;
        }
    }

    if(hasRoom){
        return (
            <div className="home-container">
                <div className="select-lang">
                    <select name="language" defaultValue={lang} onChange={e => {setLang(e.target.value as 'ENG' | 'CHN'); langRef.current = e.target.value as 'ENG' | 'CHN'; cookies.set('lang', e.target.value)}}>
                        <option value="ENG">English</option>
                        <option value="CHN"></option>
                    </select>
                </div>
                <div className="room-name">
                    {lang === 'ENG' ? `You are in room ${(typeof hasRoom != "boolean") && hasRoom.name}` : `${(typeof hasRoom != "boolean") && hasRoom.name}`}
                </div>
                <div className="room-info">
    
                </div>
                <div className="room-players">
                    {(typeof hasRoom != "boolean") && hasRoom.players.map(player => {
                        if(player.name === name){
                            return (
                                <div key="1" className="room-player">
                                    <div className="name"><label>{lang === 'ENG' ? 'Name' : ''}</label><div className="inner">{lang === 'ENG' ? `${player.name} (You)` : `${player.name}`}</div></div>
                                    <div className="role"><label>{lang === 'ENG' ? 'Role' : ''}</label><div className="inner">{player.role === 'ATTACK' ? lang === 'ENG' ? 'ATTACK' : '' : lang === 'ENG' ? 'DEFEND' : ''}</div></div>
                                    <div className={player.isReady ? 'ready' : 'not-ready'}><label>{lang === 'ENG' ? 'Status' : ''}</label><div className="inner">{player.isReady ? lang === 'ENG' ? 'Ready' : '' : lang === 'ENG' ? 'Not ready' : ''}</div></div>
                                </div>
                            )}})
                    }
                    {(typeof hasRoom != "boolean") && hasRoom.players.map(player => {
                        if(!(player.name === name)){
                            return (
                                <div key={player.name} className="room-player">
                                    <div className="name"><label>{lang === 'ENG' ? 'Name' : ''}</label><div className="inner">{player.name}</div></div>
                                    <div className="role"><label>{lang === 'ENG' ? 'Role' : ''}</label><div className="inner">{player.role === 'ATTACK' ? lang === 'ENG' ? 'ATTACK' : '' : lang === 'ENG' ? 'DEFEND' : ''}</div></div>
                                    <div className={player.isReady ? 'ready' : 'not-ready'}><label>{lang === 'ENG' ? 'Status' : ''}</label><div className="inner">{player.isReady ? lang === 'ENG' ? 'Ready' : '' : lang === 'ENG' ? 'Not ready' : ''}</div></div>
                                </div>
                            )}})
                    }
                </div>
                <div className="master-number">
                    <select defaultValue={Values.TWO} name="masterNumber" onChange={e => setMasterNumber(e.target.value)}>
                        <option value={Values.TWO}>2</option>
                        <option value={Values.THREE}>3</option>
                        <option value={Values.FOUR}>4</option>
                        <option value={Values.FIVE}>5</option>
                        <option value={Values.SIX}>6</option>
                        <option value={Values.SEVEN}>7</option>
                        <option value={Values.EIGHT}>8</option>
                        <option value={Values.NINE}>9</option>
                        <option value={Values.TEN}>10</option>
                        <option value={Values.JACK}>J</option>
                        <option value={Values.QUEEN}>Q</option>
                        <option value={Values.KING}>K</option>
                        <option value={Values.ACE}>A</option>
                        <option value={'JOKER'}>JOKER</option>
                    </select>
                </div>
                <div className="toggle-ready">
                    <button className={isReady ? 'ready' : 'not-ready'} onClick={() => toggleReady()}>{isReady ? lang === 'ENG' ? 'I\'m not ready' : '' : lang === 'ENG' ? 'I\'m ready' : ''}</button>
                </div>
            </div>
        )
    }

    return (
        <div className="home-container">
            <div className="select-lang">
                <select name="language" defaultValue={lang} onChange={e => {setLang(e.target.value as 'ENG' | 'CHN'); langRef.current = e.target.value as 'ENG' | 'CHN'; cookies.set('lang', e.target.value)}}>
                    <option value="ENG">English</option>
                    <option value="CHN"></option>
                </select>
            </div>
            <div className="form-container">
                <input placeholder={lang === 'ENG' ? 'Room' : ''} name="room" onChange={e => {setRoom(e.target.value); roomRef.current = e.target.value}}></input>
                <input placeholder={lang === 'ENG' ? 'Name' : ''} name="name" onChange={e => {setName(e.target.value); nameRef.current = e.target.value}}></input>
                <select name="role" defaultValue="ATTACK" onChange={e => setRole(e.target.value as 'ATTACK' | 'DEFEND')}>
                    <option value="ATTACK">{lang === 'ENG' ? 'Attacking team' : ''}</option>
                    <option value="DEFEND">{lang === 'ENG' ? 'Defending team' : ''}</option>
                </select>
                    <div className="alert" ref={alert}></div>
                <button type="submit" onClick={e => handleSubmit(e)}>{lang === 'ENG' ? 'Confirm' : ''}</button>
            </div>
        </div>
    )
}
